import React from "react";
import styled, { keyframes } from "styled-components";
import startPage from "../slice_img/ui/startpage_bg.jpg";
import castle from "../slice_img/ui/img_castle.svg";
import Button from "../components/Button";
import { ReactComponent as CardType1s } from "../slice_img/cards/cardtype_1s.svg";
import { ReactComponent as CardType2s } from "../slice_img/cards/cardtype_2s.svg";
import { ReactComponent as CardType3s } from "../slice_img/cards/cardtype_3s.svg";
import { ReactComponent as CardType4s } from "../slice_img/cards/cardtype_4s.svg";

const DIFFICULT_LV = ["easy", "normal", "hard"];

const MainBoxMV = keyframes`
    from {
        bottom: -250px;
    }
`;

const StartWrapper = styled.div`
  position: absolute;
  width: 100%;
  height: ${props => (props.open ? "100%" : "0")};
  top: 0;
  left: 0;
  background-color: #2c2b50;
  background-image: url(${startPage});
  background-size: auto;
  background-position: top;
  background-repeat: no-repeat;
  overflow: hidden;
  transition: 0.5s;
  z-index: 50;
`;

const BottomBg = styled.div`
  position: absolute;
  width: 100%;
  height: 27vh;
  bottom: 0;
  background: #2c2b50;
`;

const MainBox = styled.div`
  position: absolute;
  height: 450px;
  bottom: 0;
  left: 50%;
  color: #9e99ff;
  text-align: center;
  background: url(${castle});
  background-size: 60%;
  background-position: top;
  background-repeat: no-repeat;
  transform: translate(-50%);
  animation: ${MainBoxMV} 1s;
  z-index: 60;
  h1 {
    margin-top: 24px;
    font-weight: 200;
  }
  span {
    font-size: 1.25em;
    margin: 0 0.5em;
    opacity: 0.8;
    cursor: pointer;
    &:hover {
      opacity: 1;
    }
  }
`;

const MainBtn = styled(Button)`
  font-size: 3.25em;
  font-weight: 500;
  padding: 16px 50px;
  margin-top: 190px;
  border-radius: 1.5em;
  letter-spacing: 0.1em;
  box-shadow: 0 10px 30px rgba(49, 20, 78, 0.5);
`;

const TypesBox = styled.div`
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 33%;
  left: 50%;
  width: 190px;
  transform: translate(-50%, -50%);
  & svg {
    transform: scale(1.375);
    path {
      fill: #a1c8ff;
    }
  }
`;

const StartContainer = ({ open, onClick, setDifficult, nowDifficult }) => {
  return (
    <StartWrapper open={open}>
      <MainBox>
        <MainBtn text='START' onClick={onClick} />
        {DIFFICULT_LV.map((item, index) => {
          const active = nowDifficult === index ? "active" : "";
          return (
            <span key={`difficult${index}`} onClick={() => setDifficult(index)} active={active}>
              {item}
            </span>
          );
        })}
        <h1>How to play?</h1>
      </MainBox>
      <TypesBox>
        <CardType1s />
        <CardType2s />
        <CardType3s />
        <CardType4s />
      </TypesBox>
      <BottomBg />
    </StartWrapper>
  );
};

export default StartContainer;
